// 区域选择框
<template>
  <div>
     <span id='text'>区域选择:</span>
            <el-select v-model="areaValue" placeholder="请选择" :style="widthSet" @change="submitArea"  filterable>
              <el-option
                v-for="item in areaList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
  </div>
  
</template>
<script>
import axios from 'axios'
export default { 
  props:['areaSet'],
  // props:['areaName'],
  data(){
    return {
      areaValue:'全部',//选择的区域
      areaList:[],//区域列表
      textWidth:''

    }
  },
  components:{

  },
  mounted(){
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getArea').then(res=>{
        //   console.log(res.data)
          this.areaList=res.data.list
         
      })
        this.textWidth=document.getElementById('text').offsetWidth

  },
  methods:{
    submitArea(val){
      this.$emit('areaGet',val)
    }
  },
  computed:{
    widthSet(){
      return `width:calc(90% - ${this.textWidth}px);min-width:0px;`
    }
  },
  watch:{
    areaSet(val){
      this.areaValue=val
    }
  }

}
</script>
