<template>
  <div>
    <el-card shadow="hover" :body-style="{ padding: '0px' }">
                            <div :style="content">
                                <i :class="el-icon-user-solid" :style="gridIcon"></i>
                                <div class="grid-cont-right">
                                    <div :style="gridVal">{{options&&options.content&&options.content.value&&options.content.value.value?options.content.value.value:'未传值'}}</div>
                                    <div>{{options&&options.content&&options.content.title&&options.content.title.title?options.content.title.title:'未传值'}}</div>
                                    <div>{{}}</div>
                                </div>
                            </div>
                        </el-card>
    
  </div>
</template>
<script>
export default {
  data(){
    return {
      tag:{
        body:{
          height:'',
        },
        content:{
          value:{
              color:'',
              value:'',
              size:''
                },
          title:{
              color:'',
              title:''
                },
          compare:{
              value:'',
              
          }
        },
        icon:{
          color:'',
          name:'',
          width:'',
          bgcolor:'',
          iconSize:''
        },
      }
    }
  },
  props:['options']
  ,
  mounted(){

  },
  computed:{
    content(){
        let boolVar=this.options&&this.options.body
      return `display: flex;
              align-items: center;
              height: ${boolVar&&this.options.body.height?this.options.body.height:'150px'};
        `
    },
    gridIcon(){
        let boolVar1=this.options&&this.options.icon
        let boolVar2=this.options&&this.options.body
      return `
            font-size:${boolVar1&&this.options.icon.iconSize?this.options.icon.iconSize:'70px'} ;
            width:${boolVar1&&this.options.icon.width?this.options.icon.width:'20%'} ;
            height:${boolVar2&&this.options.body.height?this.options.body.height:'150px'};
            text-align: center;
            line-height:${boolVar2&&this.options.body.height?this.options.body.height:'150px'};
            color:${boolVar1&&this.options.icon.color?this.options.icon.color:'#fff'} ;
            background:${boolVar1&&this.options.icon.bgcolor?this.options.icon.bgcolor:'rgb(45, 140, 240)'} ;
      `
    },
    gridVal(){
        let boolVar=this.options&&this.options.value
      return `
            font-size:${boolVar&&this.options.value.size?this.options.value.size:'40px'} ;
            font-weight: bold;
            color:rgb(45, 140, 240);
      `
    }
  }
}
</script>
<style scoped>
/* 浮动 */
.grid-content {
    display: flex;
    align-items: center;
    height: 150px;
}

.grid-cont-right {
    flex: 1;
    text-align: center;
    font-size: 20px;
    color: #999;
}

.grid-num {
    font-size: 40px;
    font-weight: bold;
}

.grid-con-icon {
    font-size: 70px;
    width: 150px;
    height: 150px;
    text-align: center;
    line-height: 150px;
    color: #fff;
}

.grid-con-1 .grid-con-icon {
    background: rgb(45, 140, 240);
}
.grid-con-1 .grid-num {
    color: rgb(45, 140, 240);
}
</style>