//客流明细
<template>
  <div>
      <crumbs menu='商场流量' subMenu='客流明细' /> 
      <div class="container sql">
        <el-row type="flex" justify="space-around">
          <el-col :span='6'>
            <market @marketGet='marketGet' />
          </el-col>
          <el-col :span='6'>
            <index-select :indexSet='index' @indexGet='indexGet' />
          </el-col>
          <el-col :span='6'>
            <area-select @areaGet='areaGet'/>
          </el-col>
          <el-col :span='6'>
            <el-button-group>
              <el-button type="primary" icon="el-icon-search" @click='onQuery'>查询</el-button>
              <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
              <export-excel file-name="数据" :header="['序号','区域名称','总客流量','集团占比','当月同比','集客力','日均客流量','游逛深度','提袋率','商场名称','开始时间','结束时间']"
              :filter-filed="['id','areaType','allPaFlow','enterHas','monthHas','getPower','avgFlow','deep','rate','marketName','startTime','endTime']" :data="exportData()">导出</export-excel>
            </el-button-group>
          </el-col>
        </el-row>
      </div>
      <el-row>
        <el-col :span='12'>
          <el-card style="font-weight: bold;margin-top:10px" id="card">
            <template #header>
              <div class="card-header">
                <el-col :span='2' >
                  <span class="text fontCenter">热力图</span>
                </el-col>
                <el-col :span='6'>
                  <el-col :span='8' >
                  <span style="line-height: 40px;">起始时间</span>
                  </el-col>
                  <el-col :span='16'>
                    <el-time-select
                      @change="stepChange"
                      placeholder="起始时间"
                      v-model="hotMap.el_time_select.startTime"
                      :start='hotMap.market.startTime'
                      :step='hotMap.el_time_select.step'
                      :end='hotMap.market.endTime'>
                    </el-time-select>
                  </el-col>
                  
                </el-col>
                <el-col :span='6'>
                  <el-col :span='8' >
                      <span style="line-height: 40px;">结束时间</span>
                  </el-col>
                  <el-col :span='16' >
                      <el-time-select
                        @change="stepChange"
                        placeholder="结束时间"
                        v-model="hotMap.el_time_select.endTime"
                        :start='hotMap.market.startTime'
                        :step='hotMap.el_time_select.step'
                        :end='hotMap.market.endTime'
                        :minTime="hotMap.el_time_select.startTime">
                      </el-time-select>
                  </el-col>
                </el-col>
                <el-col :span='6'>
                  <el-col :span='8' >
                    <span style="line-height: 40px;">时间间隔</span>
                  </el-col>
                  <el-col :span='16' >
                    <el-time-select
                      @change="stepChange"
                      v-model="hotMap.datastep"
                      start='00:05'
                      step='00:05'
                      end='1:00'
                      placeholder="间隔选择">
                    </el-time-select>
                  </el-col>
                  
                </el-col>
              </div>
            </template>
          <div ref="hotMap" style="height:350px;width:100%;" >
          </div>
          </el-card>
        </el-col>
        <el-col :span='12'>
            <el-card shadow="hover" class="marginTop10 marginLeft10">
              <el-row>
                  <el-col :span='15'>
                  <time-select @timeGet='timeGet' ></time-select>
                </el-col>
                  <el-col :span='9'>
                    <div class="block">
                      <el-date-picker
                        @change="timeChange"
                        style='width:100%;'
                        :disabled='datePickerAble'
                        v-model="timeRange"
                        type="daterange"
                        :shortcuts="shortcuts"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        align="right">
                      </el-date-picker>
                  </div>
                </el-col>
              </el-row>
              <el-table class="font-set"
                :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                style="width: 100%;margin-bottom: 10px;margin-top:10px;font-size: 9px"
                row-key="id"
                border
                :current-page="currentPage"
                :max-height='305'
                stripe
                >
                <el-table-column
                  prop="id"
                  label="序号"
                  sortable
                  min-width="9%">
                </el-table-column>
                <el-table-column
                  prop="areaType"
                  label="区域名称"
                  sortable
                  min-width="10%">
                </el-table-column>
                <el-table-column
                  prop="allPaFlow"
                  label="总客流量"
                  sortable
                  min-width="10%"
                  >
                  
                </el-table-column>
                <el-table-column
                  prop="enterHas"
                  label="集团占比"
                  sortable
                  min-width="10%">
                </el-table-column>
                <el-table-column
                  prop="monthHas"
                  label="当月同比"
                  sortable
                  min-width="10%">
                </el-table-column>
                <el-table-column
                  prop="getPower"
                  label="集客力"
                  sortable
                  min-width="9%">
                </el-table-column>
                <el-table-column
                  prop="avgFlow"
                  label="日均客流量"
                  sortable
                  min-width="10%">
                </el-table-column>
                <el-table-column
                  prop="deep"
                  label="游逛深度"
                  sortable
                  min-width="10%">
                </el-table-column>
                <el-table-column
                  prop="rate"
                  label="提袋率"
                  sortable
                  min-width="12%">
                </el-table-column>
              </el-table>
              <div class="pagination">
              <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :page-sizes="[10]"
                  :page-size="pagesize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="total">     
              </el-pagination>
          </div>
            </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <!-- <chart :option='chart2.options' :title='chart2.title'/> -->
          <el-card class="marginTop10" shadow="hover">
            <template #header>
              <div >
                <el-row style="display:flex;flex-wrap:nowrap;">
                  <span class="text">总流量明细</span>
                  <!-- <el-col :span='10' :push='1'>
                    <div class="block" >
                      <span class="demonstration" id="text">时间选择:</span>
                        <el-date-picker
                        style="width:60%;"
                          @change="detailTimeChange"
                          v-model="compareTime"
                          align="right"
                          type="date"
                          placeholder="选择日期"
                          :disabled-date="disabledDate"
                          :shortcuts="compareShortcuts"
                        >
                        </el-date-picker>
                      </div>
                  </el-col>
                  <el-col :span='10' >
                    <market @marketGet='compareMarketGet'/>
                  </el-col> -->
                </el-row>
              </div>
            </template>
            <div id='tag'>
                <el-row >
                <el-col :span='8'>
                  <small-tag :options='tagSet.allPaFlow' class="marginLeft10"/>
                </el-col>
                <el-col :span='8' >
                  <small-tag :options='tagSet.enterHas' class="marginLeft10"/>
                </el-col>
                <el-col :span='8'>
                  <small-tag :options='tagSet.monthHas' class="marginLeft10" />
                </el-col>
              </el-row>
              <el-row class="marginTop10" style="padding-bottom:18px;">
                <el-col :span='8'>
                  <small-tag :options='tagSet.getPower' class="marginLeft10"/>
                </el-col>
                <el-col :span='8' >
                  <small-tag :options='tagSet.avgFlow' class="marginLeft10"/>
                </el-col>
                <el-col :span='8'>
                  <small-tag :options='tagSet.deep' class="marginLeft10"/>
                </el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card style="font-weight: bold;margin-top:10px" id="card" class="marginLeft10">
            <template #header>
              <div class="card-header">
                <el-col :span='4' >
                  <span class="text fontCenter">时段流量明细</span>
                </el-col>
                <el-col :span='6'>
                  <el-col :span='11' >
                  <span style="line-height: 40px;">起始时间</span>
                  </el-col>
                  <el-col :span='13'>
                    <el-time-select
                      @change="duringChange"
                      placeholder="起始时间"
                      v-model="detail.el_time_select.startTime"
                      :start='detail.market.startTime'
                      :step='detail.el_time_select.step'
                      :end='detail.market.endTime'>
                    </el-time-select>
                  </el-col>
                  
                </el-col>
                <el-col :span='6'>
                  <el-col :span='11' >
                      <span style="line-height: 40px;">结束时间</span>
                  </el-col>
                  <el-col :span='13' >
                      <el-time-select
                        @change="duringChange"
                        placeholder="结束时间"
                        v-model="detail.el_time_select.endTime"
                        :start='detail.market.startTime'
                        :step='detail.el_time_select.step'
                        :end='detail.market.endTime'
                        :minTime="detail.el_time_select.startTime">
                      </el-time-select>
                  </el-col>
                </el-col>
                <el-col :span='6'>
                  <el-col :span='11' >
                    <span style="line-height: 40px;">时间间隔</span>
                  </el-col>
                  <el-col :span='13' >
                    <el-time-select
                      @change="duringChange"
                      v-model="detail.datastep"
                      start='00:05'
                      step='00:05'
                      end='1:00'
                      placeholder="间隔选择">
                    </el-time-select>
                  </el-col>
                  
                </el-col>
              </div>
            </template>
          <div ref="detail" style="height:273px;width:100%;" >
          </div>
          <el-radio-group v-model="detail.indexName" class="detailIndex" @change="detailIndexChange">
            <el-radio-button label="客流量"></el-radio-button>
            <el-radio-button label="集团占比"></el-radio-button>
            <el-radio-button label="当月同比"></el-radio-button>
            <el-radio-button label="集客力"></el-radio-button>
            <el-radio-button label="日均客流量"></el-radio-button>
            <el-radio-button label="游逛深度"></el-radio-button>
            <el-radio-button label="提袋率"></el-radio-button>
          </el-radio-group>
          </el-card>
        </el-col>
      </el-row>
      <el-dialog title="收货地址" v-model="radarMap.dialogFormVisible" @open='open()' width="35%">
        <template #title >
          <div>
            <el-row class="borderBottom marginBottom">
              <el-col :span='8' style='line-height: 40px;'>
                <span class="text fontCenter">雷达图</span>
              </el-col>
              <el-col :span='8'>
                <market @marketGet='marketGetRadar' />
              </el-col>
              <el-col :span='8' style="display:flex;justify-content:flex-end;">
                <el-button type="primary" @click="radarMap.dialogFormVisible = false">关闭</el-button>
              </el-col>
            </el-row>
          </div>
        </template>
          <div ref="radarMap" style="height:350px;width:100%;" >
          </div>
      </el-dialog>
            

  </div>
  
</template>
<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import IndexSelect from '../pages/indexSelect/IndexSelect'
import AreaSelect from '../pages/indexSelect/AreaSelect'
import TimeSelect from '../pages/TimeSelectNoText'
import smallTag from '../pages/smallTag'
import ExportExcel from '../pages/excel/ExportExcel'
import axios from 'axios'
import * as echarts from 'echarts'
export default {
  data() {
        return {
            cardHeight:'',
            marketName:'万达',//选择的商场名称
            compareMarketName:'万达',//对比的商场名称
            radarMapMarketName:'万达',
            indexName:'客流量',//选择的指标
            areaName:'全部',//选择的区域
            datePickerAble:true,
            time:'',//选择的时间
            timeRange: '',//选择的时间范围
            tableData:[],//表格数据
            currentPage:1,//当前页
            pagesize:10,//每页数量
            total:0,//
            index:[{
                      value: '客流量',
                      label: '客流量',
                    }, {
                      value: '集客力',
                      label: '集客力',
                    }, {
                      value: '提袋率',
                      label: '提袋率',
                    }, {
                      value: '游逛深度',
                      label: '游逛深度',
                    }],
            shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [start, end]
                })()
              }, {
                text: '最近30天',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [start, end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [start, end]
                })()
              }],
            disabledDate(time) {
              return time.getTime() > Date.now()
            },
            compareShortcuts: [{
              text: '今天',
              value: new Date(),
            }, {
              text: '昨天',
              value: (() => {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24)
                return date
              })(),
            }, {
              text: '一周前',
              value: (() => {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
                return date
              })(),
            },{
                text: '一月前',
                value: (() => {
                  const date = new Date();
                  date.setTime(date.getTime() - 3600 * 1000 * 24 * 30);
                  return date
                })()
              }, {
                text: '一年前',
                value: (() => {
                  const date = new Date();
                  date.setFullYear(date.getFullYear()-1)
                  // date.setTime(date.getTime() - 3600 * 1000 * 24 * 90);
                  return date
                })()
              }
            
            ],
            compareTime:'',
            screenWidth: document.body.clientWidth,   
            //左下
            tagSet:{
              allPaFlow:{
                icon:{
                  width:'20%',
                  bgcolor:'#67C23A'
                },
                content:{
                  title:{
                    title:'总客流量'
                  },
                  value:{
                    value:''
                  }
                }
              },
              enterHas:{
                icon:{
                  width:'20%',
                  bgcolor:'#e89a3c'
                },
                content:{
                  title:{
                    title:'集团占比'
                  },
                  value:{
                    value:''
                  }
                }
              },
              monthHas:{
                icon:{
                  width:'20%',
                  bgcolor:'#a9d134'
                },
                content:{
                  title:{
                    title:'当月同比'
                  },
                  value:{
                    value:''
                  }
                }
              },
              getPower:{
                icon:{
                  width:'20%',
                  bgcolor:'#d8bd14'
                },
                content:{
                  title:{
                    title:'集客力'
                  },
                  value:{
                    value:''
                  }
                }
              },
              avgFlow:{
                icon:{
                  width:'20%',
                  bgcolor:'#33bcb7'
                },
                content:{
                  title:{
                    title:'日均客流量'
                  },
                  value:{
                    value:''
                  }
                }
              },
              deep:{
                icon:{
                  width:'20%',
                  bgcolor:'#49aa19'
                },
                content:{
                  title:{
                    title:'游逛深度'
                  },
                  value:{
                    value:''
                  }
                }
              },
              textWidth:''
            },
            //左上
            hotMap:{
              el_time_select:{
                startTime:'9:00',
                endTime:'21:00',
                step:'00:15',
              },
              market:{
                startTime:'',
                endTime:''
              },
              datastep:'01:00',
              chart:{},
              chartData:[],
              max:''
            },
            //右下角
            detail:{
              el_time_select:{
                startTime:'9:00',
                endTime:'21:00',
                step:'00:15',
              },
              market:{
                startTime:'',
                endTime:''
              },
              datastep:'01:00',
              chart:{},
              chartData:[],
              max:'',
              indexName:'客流量'
            },
            radarMap:{
              dialogFormVisible: false,
              time:{
                day:'',
                time:''
              },
              data:{
                seriesName:'',
                data:''
              },
              chart:{},
              chartData:[]
            }
        };

    },
  methods: {
    exportData(){
      for(let i=0;i<this.tableData.length;i++){
        this.tableData[i].marketName=this.marketName
        this.tableData[i].startTime=this.$moment(this.timeRange[0]).format('YYYY-MM-DD')
        this.tableData[i].endTime=this.$moment(this.timeRange[1]).format('YYYY-MM-DD')
      }
      return this.tableData
    },
      marketGetRadar(val){
        this.radarMapMarketName=val
      },
      marketGet(val){
      this.marketName=val
      },
      compareMarketGet(val){
      this.compareMarketName=val
      },
      indexGet(val){
        this.indexName=val
      },
      areaGet(val){
        this.areaName=val
      },
      timeGet(val){
        this.time=val
        if(val=='自定义时间'){
          this.datePickerAble=false
        }else this.datePickerAble=true
      },
      handleSizeChange(val) {
            this.pagesize=val;
        },
      handleCurrentChange(val) {
            this.currentPage = val;
        },
      onQuery(){
        this.stepChange()
        this.tableChange()
        this.detailTimeChange()
        this.duringChange()
      },
      reSet(){
        // this.marketName='万达'
        // this.indexName='客流量'
        // this.areaName='全部'
        // this.$forceUpdate()
      },
      timeIntval(startTime,endTime){
          let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD')
          endTime = moment(endTime).format('YYYY-MM-DD')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('MM-DD'))
            startTime = moment(startTime).add(1, 'days').format('YYYY-MM-DD')
          }
          arr.push(moment(endTime).format('MM-DD'))
          return arr
      },
      workIntval(startTime,endTime,step){
        step=step.split(':')
        startTime=`2017-01-01 ${startTime}`
        endTime=`2017-01-01 ${endTime}`

        let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD HH:mm')

          endTime = moment(endTime).format('YYYY-MM-DD HH:mm')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('HH:mm'))
            startTime = moment(startTime).add({hours:parseInt(step[0]),minutes:parseInt(step[1])}).format('YYYY-MM-DD HH:mm')
  
          }
          arr.push(moment(endTime).format('HH:mm'))
          return arr
      },
      initChart1(chart,data){
            let myChart =echarts.init(chart);
            let option = {
              title:{
                show:true,
                text:this.$moment(this.timeRange[0]).format('YYYY-MM-DD') +'至'+this.$moment(this.timeRange[1]).format('YYYY-MM-DD')+this.indexName,
                x:'center',
                y:'top',
                textStyle:{
                  fontSize:'14',
                  fontWeight:'bolder'
                }
              },
                  tooltip: {
                      position: 'top',
                    
                      axisPointer: {
                          type: 'cross',
                          crossStyle: {
                              color: '#999'
                          }
                      },
                    

                  },
                  dataZoom: [{
                      type: 'slider',
                      xAxisIndex: [0],
                  }, {
                      type: 'slider',
                      yAxisIndex: [0],
                  },{
                    type:'inside',
                      xAxisIndex: [0],
                  },{
                    type:'inside',
                      yAxisIndex: [0],
                  }],
                  toolbox: {
                      feature: {
                        dataZoom: {
                              // yAxisIndex: true,
                              // xAxisIndex:true
                          },
                          dataView: {show: true, readOnly: false},
                          magicType: {show: true, type: ['line', 'bar']},
                          saveAsImage: {show: true}, 
                      }
                  },
                  grid: {
                      // height: '70%',
                      // top: '5%'
                      // left:'15%',
                      // right:'15%',
                    containLabel:true
                  },
                  xAxis: {
                      type: 'category',
                      data: this.workIntval(this.hotMap.el_time_select.startTime,this.hotMap.el_time_select.endTime,this.hotMap.datastep),
                      splitArea: {
                          show: true
                      }
                  },
                  yAxis: {
                      type: 'category',
                      data: this.timeIntval(this.timeRange[0],this.timeRange[1]),
                      splitArea: {
                          show: true
                      }
                  },
                  visualMap: data[0].visualMap,
                  series: [data[0].series]
              };

            myChart.setOption(option);
            return myChart
      },
      initChart2(chart,data){
            let myChart =echarts.init(chart);
            let option = {
                  title:{
                    show:true,
                    text:this.$moment(this.timeRange[0]).format('YYYY-MM-DD')+'至'+this.$moment(this.timeRange[1]).format('YYYY-MM-DD')+this.detail.indexName,
                    x:'center',
                    y:'top',
                    textStyle:{
                      fontSize:'14',
                      fontWeight:'bolder'
                    }
                  },
                  tooltip: {
                      position: 'top',
                      axisPointer: {
                          type: 'cross',
                          crossStyle: {
                              color: '#999'
                          }
                      },
                  },
                  legend: {
                      data:this.detail.indexName
                  },
                  toolbox: {
                      show: true,
                      feature: {
                          dataView: {show: true, readOnly: false},
                          magicType: {show: true, type: ['line', 'bar']},
                          restore: {show: true},
                          saveAsImage: {show: true}
                      }
                  },
                  calculable: true,
                  xAxis: [
                      {
                          type: 'category',
                          data: this.workIntval(this.detail.el_time_select.startTime,this.detail.el_time_select.endTime,this.detail.datastep)
                      }
                  ],
                  yAxis: [
                      {
                          type: 'value'
                      }
                  ],
                  series: data
              };

            myChart.setOption(option);
            return myChart
      },
      initRardar(chart,data){
            let myChart =echarts.init(chart);
            let option = {
                  title:{
                    show:true,
                    text:this.radarMap.time.day+'  '+this.radarMap.time.time+'   雷达图',
                    x:'center',
                    y:'top',
                    textStyle:{
                      fontSize:'14',
                      fontWeight:'bolder'
                    }
                  },
                  calculable: true,

                  tooltip: {
                      position: 'top',
                      axisPointer: {
                          type: 'cross',
                          crossStyle: {
                              color: '#999'
                          }
                      },
                  },
                  legend: {
                      data: [this.marketName,this.radarMapMarketName],
                      top:'bottom'
                  },
                  toolbox: {
                      show: true,
                      feature: {
                          dataView: {show: true, readOnly: false},
                          magicType: {show: true, type: ['line', 'bar']},
                          restore: {show: true},
                          saveAsImage: {show: true}
                      }
                  },
                  radar: [
                      {
                          indicator: [
                              {text: '客流量'},
                              {text: '集客力'},
                              {text: '提袋率'},
                              {text: '游逛深度'}
                          ],
                          center: ['50%', '50%'],
                          radius: 120
                      }
                      
                  ],
                  series: data
              };

            myChart.setOption(option);
            return myChart
      },
      //hotMap间隔
      stepChange(){
        if(this.hotMap.chart.clear()){
          this.hotMap.chart.clear()
        }
        
        this.hotMap.chartData=[]
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getHotTest?time=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}&step=${this.workIntval(this.hotMap.el_time_select.startTime,this.hotMap.el_time_select.endTime,this.hotMap.datastep).length}`).then(res=>{
            //时间初始化
            //获取最大最小值
            let minMax=[]   
            //数据整理
            let propsName=''
            if(this.indexName=='客流量'){
              propsName='flow'
            }else if(this.indexName=='集客力'){
              propsName='getPower'
            }else if(this.indexName=='提袋率'){
              propsName='rate'
            }else if(this.indexName=='游逛深度'){
              propsName='deep'
            }
            let newArr=[]
            for(let i=0;i<res.data.list[propsName].length;i++){
              for(let j=0;j<res.data.list[propsName][i].length;j++){
                minMax.push(res.data.list[propsName][i][j])
                newArr.push([
                  j,//小时数据-列
                  i,//日期数据-行
                  res.data.list[propsName][i][j]//指标值
                ])
              }
            }
            
            this.hotMap.chartData.push({
              visualMap:{
                min:Math.min(...minMax),
                max:Math.max(...minMax),
                calculable: true, 
                orient: 'vertical',
                align:'auto',
                left: 'left',
                itemHeight:'250',
                top:'middle'
              },
              series:{
                name: this.indexName,
                type: 'heatmap',
                data: newArr,
                label: {
                      show: false,
                },
                emphasis: {
                  itemStyle: {
                      shadowBlur: 10,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                              }
                }
              }
            })
            this.hotMap.chart=this.initChart1(this.$refs.hotMap,this.hotMap.chartData)
          })
      },
      //时间范围选择器
      timeChange(){
        this.stepChange()
        this.tableChange()
        this.detailTimeChange()

      },
      //上右表格
      tableChange(){
        axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getChartData').then(res=>{
          
          if(this.areaName=='全部'){
              this.tableData=res.data.result.list
            }else{
            this.tableData=res.data.result.list.filter(item=>item.areaType==this.areaName)
            }
          this.total=this.tableData.length
    })
      },
      //左下角改变
      detailTimeChange(){
        axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getChartData').then(res=>{
        let dataList=''
        if(this.areaName=='全部'){
          dataList=res.data.result.list
        }else{
          dataList=res.data.result.list.filter(item=>item.areaType==this.areaName)
        }
        this.tagSet.allPaFlow.content.value.value= dataList.reduce((pre,curr)=>pre+curr.allPaFlow,0)
        this.tagSet.enterHas.content.value.value=dataList[0].enterHas
        this.tagSet.monthHas.content.value.value=dataList[0].monthHas
        this.tagSet.getPower.content.value.value=dataList.reduce((pre,curr)=>pre+curr.getPower,0).toFixed(2)
        this.tagSet.avgFlow.content.value.value=dataList.reduce((pre,curr)=>pre+curr.avgFlow,0)
        this.tagSet.deep.content.value.value=dataList.reduce((pre,curr)=>pre+curr.deep,0)
    })

      },
      //右下角改变
      duringChange(){
        if(this.detail.chart.clear()){
          this.detail.chart.clear()
        }
        this.detail.chartData=[]
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getPaFlowDetails?time=${this.workIntval(this.detail.el_time_select.startTime,this.detail.el_time_select.endTime,this.detail.datastep).length}`).then(res=>{
          let propsName=''
          if(this.detail.indexName=='客流量'){
            propsName='flow'
          }else if(this.detail.indexName=='集客力'){
            propsName='getPower'
          }else if(this.detail.indexName=='提袋率'){
            propsName='rate'
          }else if(this.detail.indexName=='集团占比'){
            propsName='enterHas'
          }else if(this.detail.indexName=='当月同比'){
            propsName='monthHas'
          }else if(this.detail.indexName=='日均客流量'){
            propsName='avgFlow'
          }else if(this.detail.indexName=='游逛深度'){
            propsName='deep'
          }
          console.log(res.data.list)
          this.detail.chartData.push({
                          name:this.detail.indexName ,
                          type: 'bar',
                          itemStyle:{
                            color: '#49aa19'
                          },
                          // barWidth:70,
                          data: res.data.list[propsName],
                          markPoint: {
                              data: [
                                  {type: 'max', name: '最大值'},
                                  {type: 'min', name: '最小值'}
                              ]
                          },
                          markLine: {
                              data: [
                                  {type: 'average', name: '平均值'}
                              ]
                          }
                      })
      this.detail.chart=this.initChart2(this.$refs.detail,this.detail.chartData)
    })
      },
      detailIndexChange(){
        this.duringChange()
      },
      //计算雷达图所处日期与小时
      calcDayAndTime(data){
        // this.radarMap.time.day=this.timeIntval(this.timeRange[0],this.timeRange[1])[data.data[1]]
        this.radarMap.time.day=this.$moment(this.timeRange[0]).add(data.data[1],'days').format('YYYY-MM-DD')
        this.radarMap.time.time=data.name
        this.radarMap.data.seriesName=data.seriesName
        
        let dayIndex=data.data[1]
        let value=[]
        this.radarMap.chartData=[]
        value.push(this.radarMap.data.data.flow[dayIndex][data.data[0]])
        value.push(this.radarMap.data.data.getPower[dayIndex][data.data[0]])
        value.push(this.radarMap.data.data.rate[dayIndex][data.data[0]])
        value.push(this.radarMap.data.data.deep[dayIndex][data.data[0]])
        // let max=Math.max(value[0],value[1],value[3])
        // let radar=[{
        //   indicator:[{
        //     text:'客流量',
        //   },
        //   ]
        // }]
        this.radarMap.chartData.push({
            type: 'radar',
            tooltip: {
                trigger: 'item'
            },
            areaStyle: {},
            data: [
                {
                    value: value,
                    name: this.marketName
                }
            ],
            label:{
              show: true,
              formatter: function (params) {
                return params.value;
              }        
            },
            labelLayout:{
              moveOverlap:"shiftY"
            }
        })
        console.log(this.radarMap.data)
      },
      open(){
        this.$nextTick(() => {
          this.radarMap.chart=this.initRardar(this.$refs.radarMap,this.radarMap.chartData)
      })
      }
      

  },
  components:{
    crumbs:Crumbs,
    market:MarketSelect,
